

























import Vue from "vue";
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import Reveal from "../components/Reveal.vue";
import api from "../api/index";
import IResp, { IArticle } from "../types/resp";

interface Idata {
  page: number;
  allPage:number
  loading: boolean;
  dir: IArticle[] | null;
}

export default Vue.extend({
  name:"Home",
  data(): Idata {
    return {
      page: 0,
      allPage:Number.MAX_VALUE,
      loading: false,
      dir: null
    };
  },
  created() {
    this.getDir();
  },
  methods: {
    async getDir() {
      this.loading = true;
      let res: IResp = await api.getTable(this.page);
      this.loading = false;
      if (res.remaining == 0) {
        this.allPage = this.page
      }
      this.dir = res.dir;
    },
    tranTime(str: string) {
      return new Date(str).toLocaleDateString().replace(/\//g, "-");
    },
    isRepage(n: number): boolean {
      if (n <= 0) return !!(this.page + n >= 0);
      return !!(this.allPage > this.page)

    },
    rePage(n: number) {
      if (this.isRepage(n)) {
        this.page += n;
        this.getDir();
      }
    }
  },
  components: {
    Header,
    Footer,
    Reveal
  }
});
